import React from 'react';
import './ListaProdutos.css';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

class ListaProdutos extends React.Component {
  render() {
    const { product, addCart } = this.props;
    return (
      <div className="product-list">
        {
          product.length === 0 ? 'Nenhum produto foi encontrado'
            : (
              product.map((item, index) => (

                <div key={ index } data-testid="product" className="product">

                  <Link data-testid="product-detail-link" className="item-product" to={ `/lista/${item.id}` }>
                    <p className="item-product-p">{item.title}</p>
                    <img src={ item.thumbnail } alt="Foto Produto" />
                    <p className="item-product-p">{item.price}</p>
                  </Link>

                  <button
                    data-testid="product-add-to-cart"
                    type="button"
                    className="button"
                    name={ JSON.stringify(item) }
                    onClick={ addCart }
                  >
                    Adicionar ao carrinho
                  </button>
                </div>
              ))
            )
        }
      </div>
    );
  }
}

ListaProdutos.propTypes = {
  product: PropTypes.arrayOf.isRequired,
  addCart: PropTypes.func.isRequired,
};

export default ListaProdutos;
