import React from 'react';

import { Link } from 'react-router-dom';
import Cart from './Cart';

class Header extends React.Component {
  constructor() {
    super();
    this.atualizaMenu = this.atualizaMenu.bind(this);
    this.state = {
      menusup: 'menususpenso',

    };
  }

  atualizaMenu(e) {
    if (e.checked) {
      this.setState({ menusup: 'container-menu' });
    }
    if (!e.checked) {
      this.setState({ menusup: 'menususpenso' });
    }
  }

  render() {
    const { menusup } = this.state;
    const { clickDecrement, clickIncrement, productList } = this.props;
    return (
      <header className="header">
        <Link className="cart-ancor" to="/">Home</Link>

        <Link
          data-testid="shopping-cart-button"
          className="cart-ancor"
          to="/cart"
        >
          Cart
        </Link>
        <label htmlFor="check1" className="label-menu">
          <input
            type="checkbox"
            id="check1"
            className="menususpenso"
            onClick={ (e) => { this.atualizaMenu(e.target); } }
          />
          List
        </label>
        <div className={ menusup }>
          <Cart
            productList={ productList }
            clickDecrement={ clickDecrement }
            clickIncrement={ clickIncrement }
          />
        </div>
      </header>
    );
  }
}

export default Header;
