import React from 'react';
import './App.css';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from './component/Home';
import Cart from './component/Cart';
import Colecao from './component/Colecao';
import Header from './component/Header';

class App extends React.Component {
  constructor() {
    super();
    this.state = ({
      productList: [],
    });
  }

  addCart = async ({ target }) => {
    const { name } = target;
    const productEnterInCart = JSON.parse(name);
    productEnterInCart.quantity = 1;

    this.setState((prevState) => ({
      productList: [...prevState.productList, productEnterInCart],
    }));
  }

  clickIncrement = ({ target }) => {
    const { name } = target;
    const { productList } = this.state;

    const response = productList.find((e) => e.id === name);

    response.quantity += 1;
    console.log(this.state);
    this.setState((prevState) => ({
      productList: prevState.productList,
    }));
  }

  clickDecrement = ({ target }) => {
    const { name } = target;
    const { productList } = this.state;

    const response = productList.find((e) => e.id === name);

    response.quantity = (response.quantity < 2) ? 1 : response.quantity - 1;
    console.log(this.state);
    this.setState((prevState) => ({
      productList: prevState.productList,
    }));
  }

  render() {
    const { productList } = this.state;
    return (
      <BrowserRouter>
        <Header
          className="header"
          productList={ productList }
          clickDecrement={ this.clickDecrement }
          clickIncrement={ this.clickIncrement }
        />
        <div className="route">

          <Route exact path="/Store" render={ () => <Home addCart={ this.addCart } /> } />

          <Route
            exact
            path="/cart"
            render={ () => (
              <Cart
                productList={ productList }
                clickDecrement={ this.clickDecrement }
                clickIncrement={ this.clickIncrement }
              />
            ) }
          />

          <Route
            exact
            path="/lista/:id"
            render={ (props) => (
              <Colecao
                { ...props }
                addCart={ this.addCart }
                productList={ productList }
              />
            ) }
          />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
