import React from 'react';
import PropTypes from 'prop-types';
import './Home.css';
import Barralateral from './Barralateral';
import ListaProdutos from './ListaProdutos';
import { getProductsFromCategoryAndQuery } from '../services/api';
import Form from './Form';

class Home extends React.Component {
  constructor() {
    super();

    this.state = {
      search: '',
      category: '',
      product: [],
    };
  }

  onSaveButton = async () => {
    // Quando clicar no botão pesquisar, vai ser feita requisição na API do MLB
    const { search, category } = this.state;

    if (search.length > 1 || category !== '') {
      const item = await getProductsFromCategoryAndQuery(category, search);
      this.setState({
        product: item.results,
      });
    }
  }

  inputChangeValue = ({ target }) => {
    // função usada para alterar valor no input e no state
    const { value, name } = target;
    this.setState({
      [name]: value,
    });
  }

  getCategory = ({ target }) => {
    // Função usada para escolher a categoria
    const { name } = target;
    this.setState({
      category: name,
    }, this.onSaveButton);
  }

  render() {
    const { search, product, category } = this.state;
    const { addCart } = this.props;
    return (
      <main id="main-home">

        <Barralateral
          category={ category }
          getCategory={ this.getCategory }
        />

        <section className="home-content">

          <Form
            search={ search }
            inputChangeValue={ this.inputChangeValue }
            onSaveButton={ this.onSaveButton }
          />

          {
            product.length === 0 && (
              <p data-testid="home-initial-message">
                Digite algum termo de pesquisa ou escolha uma categoria.
              </p>
            )
          }

          <ListaProdutos
            category={ category }
            product={ product }
            addCart={ addCart }
          />

        </section>
      </main>
    );
  }
}

Home.propTypes = {
  addCart: PropTypes.func.isRequired,
};

export default Home;
