export async function getCategories() {
  // Implemente aqui
  return fetch('https://api.mercadolibre.com/sites/MLB/categories').then((data) => data.json()).then((data) => data);
}

export async function getProductsFromCategoryAndQuery(categoryId, query) {
  // Implemente aqui! Quando o fizer, descomente os parâmetros que essa função recebe
  return fetch(`https://api.mercadolibre.com/sites/MLB/search?category=${categoryId}&q=${query}`).then((data) => data.json()).then((data) => data);
}

export async function Musiccard(id) {
  // Implemente aqui! Quando o fizer, descomente os parâmetros que essa função recebe
  return fetch(`https://api.mercadolibre.com/items/${id}`).then((data) => data.json()).then((data) => data);
}
