import React from 'react';
import PropTypes from 'prop-types';

class Cart extends React.Component {
  render() {
    const { productList, clickDecrement, clickIncrement } = this.props;
    return (
      <div className="container-cart">
        {
          (productList?.length > 0) ? (

            productList.map((item, index) => (

              <div key={ index } className="cart" id={ index }>
                <h3
                  data-testid="shopping-cart-product-name"
                  className="h3"
                >
                  {item.title}

                </h3>
                <h3>{item.price}</h3>
                <img
                  alt={ item.title }
                  src={ item.thumbnail }
                />
                <div className="quantity-calc" id={ index }>
                  <input
                    type="button"
                    value="x"
                    onClick={ () => {
                      document.getElementById(`${index}`).parentNode
                        .removeChild(document.getElementById(`${index}`));
                    } }
                  />
                  <input
                    type="button"
                    className="decrement"
                    name={ item.id }
                    onClick={ clickDecrement }
                    data-testid="product-decrease-quantity"
                    value="-"
                  />

                  <p data-testid="shopping-cart-product-quantity" className="gap">
                    { item.quantity}
                  </p>

                  <input
                    type="button"
                    className="increment"
                    name={ item.id }
                    onClick={ clickIncrement }
                    data-testid="product-increase-quantity"
                    value="+"
                  />
                </div>
              </div>

            ))
          )
            : <p data-testid="shopping-cart-empty-message">Seu carrinho está vazio</p>
        }
      </div>
    );
  }
}

Cart.propTypes = {
  productList: PropTypes.func.isRequired,
  clickDecrement: PropTypes.func.isRequired,
  clickIncrement: PropTypes.func.isRequired,
};

export default Cart;
