import React from 'react';
import PropTypes from 'prop-types';
import { Musiccard } from '../services/api';

class Colecao extends React.Component {
  constructor() {
    super();
    this.state = {
      Preview: [],
      describre: [],
      email: '',
      mensagem: '',
      nota: 0,
      classificacao: [],
    };
  }

  async componentDidMount() {
    const { match } = this.props;
    const { id } = match.params;
    const data = await (Musiccard(id));
    this.setState({ Preview: data, describre: data.attributes });
    const recebe = JSON.parse(localStorage.getItem('classificacao'));
    this.setState({
      classificacao: recebe,
    });
  }

  salvarState = (event) => {
    const { value } = event.target;
    console.log(value);
    if (event.target.type === 'radio') {
      this.setState({
        nota: value,
      });
    }
    if (event.target.type === 'email') {
      this.setState({
        email: value,
      });
    }
    if (event.target.type === 'textarea') {
      this.setState({
        mensagem: value,
      });
    }
  };

  enviarLocalStorage = () => {
    const { nota, email, mensagem } = this.state;
    const classificacao = { nota, email, mensagem };
    console.log(classificacao);
    const recebe = localStorage.getItem('classificacao');
    console.log(recebe);
    if (recebe === null) {
      localStorage.setItem('classificacao', JSON.stringify([classificacao]));
      this.atualiza();
    } else {
      localStorage.setItem('classificacao', JSON.stringify(
        [...JSON.parse(localStorage.getItem('classificacao')), classificacao],
      ));
      this.atualiza();
    }
  }

  atualiza() {
    const recebe = JSON.parse(localStorage.getItem('classificacao'));
    this.setState({
      classificacao: recebe,
    });
  }

  render() {
    const { Preview, describre, classificacao } = this.state;
    const { addCart } = this.props;
    return (
      <div data-testid="product-detail-name">
        <div className="container">
          <div className="container-detalhe-1">
            <p>{Preview.title}</p>
            <img src={ Preview.thumbnail } alt="Foto Produto" className="foto" />
            <button
              data-testid="product-detail-add-to-cart"
              type="button"
              name={ JSON.stringify(Preview) }
              onClick={ addCart }
              className="button"
            >
              Adicionar ao carrinho
            </button>
            <form className="container-detalhe-1">
              <input
                type="email"
                placeholder="Email"
                data-testid="product-detail-email"
                className="button input"
                onChange={ this.salvarState }
              />

              <p>Digite sua nota</p>
              <div className="score">
                <input
                  className="button input"
                  type="radio"
                  name="nota"
                  id="nota1"
                  value="1"
                  data-testid="1-rating"
                  onClick={ this.salvarState }
                />
                1
                <input
                  type="radio"
                  name="nota"
                  id="nota2"
                  value="2"
                  data-testid="2-rating"
                  onClick={ this.salvarState }
                />
                2
                <input
                  type="radio"
                  name="nota"
                  id="nota3"
                  value="3"
                  data-testid="3-rating"
                  onClick={ this.salvarState }
                />
                3
                <input
                  type="radio"
                  name="nota"
                  id="nota4"
                  value="4"
                  data-testid="4-rating"
                  onClick={ this.salvarState }
                />
                4
                <input
                  type="radio"
                  name="nota"
                  id="nota5"
                  value="5"
                  data-testid="5-rating"
                  onClick={ this.salvarState }
                />
                5
              </div>
              <textarea
                data-testid="product-detail-evaluation"
                placeholder="Mensagem (Opicional)"
                onChange={ this.salvarState }
              />
              <button
                type="button"
                data-testid="submit-review-btn"
                onClick={ this.enviarLocalStorage }
                className="button score"
              >
                Enviar

              </button>
              {classificacao?.length > 0
                ? classificacao.map((item, index) => (
                  <div key={ index } className="comentarios">
                    <div className="comentarios-rate">
                      <p>{item.email}</p>
                      <p>{item.nota}</p>
                    </div>
                    <p>{item.mensagem}</p>
                  </div>
                )) : <p />}
            </form>

          </div>
          <div className="container-detalhe">
            {describre.map((item, index) => (
              <div key={ index }>
                <p>
                  {item.name}
                  {' '}
                  :
                  {' '}
                  {item.value_name}
                </p>
                <hr />
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }
}

Colecao.propTypes = {
  match: PropTypes.func.isRequired,
  id: PropTypes.string.isRequired,
  addCart: PropTypes.func.isRequired,
};

export default Colecao;
